import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

import page_soloBox from "./components/page_soloBox"
import arrowBtn from "./components/arrowBtn"
import brdrBtn from "./components/brdr-btn"

createApp(App).use(store).component("page_soloBox", page_soloBox).component("arrowBtn", arrowBtn).component("brdrBtn", brdrBtn).use(router).mount('#app')
