<script>
export default {
    data(){return{
        navPanelOpen: false,
        iconStyle:{
            'open' : 'color: white',
            'closed' : 'color: black'
        },
    }},
}
</script>


<template>
    <div id="navBarContainer">
        <span 
        class="material-icons" 
        id="menuIcon" 
        :style="$store.state.openNav? iconStyle['open']:iconStyle['closed']"
        @click="$store.state.openNav = !$store.state.openNav"
        > menu </span>
    </div>

    <transition name='navPanelSlide'>
        <div id="navPanel" v-if="$store.state.openNav">
            <div id="navPanelContent">
                <div>
                    <div class="navLinkText" @click="$router.push({ name: 'landingPage'})">Home</div>
                    <div class="navLinkText" @click="$router.push({ name: 'logIn'})">Log In</div>
                    <div class="navLinkText" @click="$router.push({ name: 'readStory'})">Read a Story</div>
                    <div class="navLinkText" @click="$router.push({ name: 'writeStory1'})">Write a Story</div>
                    <div class="navLinkText" @click="$router.push({ name: 'storyHelp'})">Story Help</div>
                </div>
                <div class="navLinkText" @click="$router.push({ name: 'about'})">About Linked</div>
            </div>
        </div>
    </transition>
</template>

<style scoped>
#navBarContainer{
    position: absolute;
    right: 0;
    widows: 100vw;

    display: flex;
    justify-content: flex-end;
    color:white;
    align-items: center;
    user-select: none;
}

#menuIcon{
    z-index: 10;
    color: black;
    font-size: 3em;
    padding: 4px;
    transition: color 0.85s;
}
 
#menuIcon:hover{
    cursor: pointer;
}

#navPanel{
    
    z-index: 9;
    position: absolute;
    background: black;
    right: 0;
    top: 0;
    height: 100vh;
    width: 350px;
    max-width: 60vw;
    transition: transform 0.85s;
    user-select: none;
}

#navPanelContent{
    margin-top: 48px;
    padding: 8px 0 16px 16px;
    height: calc(100% - 48px);

    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.navLinkText{
    color: white;
    font-size: calc(1.4em + 0.85vw);
    margin-bottom: 16px;
}

.navLinkText:hover{
    cursor: pointer;
}

.navPanelSlide-enter-from, .navPanelSlide-leave-to /* .fade-leave-active below version 2.1.8 */ {
  transform: translateX(450px);
}
</style>